<template>
  <form class="card" @submit.prevent="submit">
    <div class="card-header">
      <h5 class="card-title">Informações para contato</h5>
    </div>
    <div class="card-body">
      <h5 class="text-center">E-mails</h5>
      <!-- Email Primário -->
      <div class="form-group">
        <label for="ipt-email1">E-mail Principal</label>
        <input id="ipt-email1" type="email" class="form-control" maxlength="45" v-model="email1" required>
      </div>
      <!-- Email Secundário -->
      <div class="form-group" v-if="qtdEmails >= 2">
        <label for="ipt-email2">E-mail Secundário</label>
        <div class="input-group">
          <input id="ipt-email2" type="email" class="form-control" maxlength="45" v-model="email2" :class="{'is-invalid': email2 && (email2 === email1 || email2 === email3)}">
          <div class="input-group-append" v-if="qtdEmails === 2">
            <button class="btn btn-danger" type="button" @click=" () => {qtdEmails--; email2=''}"><i class="fas fa-minus"></i></button>
          </div>
          <div class="invalid-feedback">E-mail repetido!</div>
        </div>
      </div>
      <!-- Email Terciário -->
      <div class="form-group" v-if="qtdEmails >= 3">
        <label for="ipt-email3">E-mail Terciário</label>
        <div class="input-group">
          <input id="ipt-email3" type="email" class="form-control" maxlength="45" v-model="email3" :class="{'is-invalid': email3 && (email3 === email1 || email3 === email2)}">
          <div class="input-group-append">
            <button class="btn btn-danger" type="button" @click=" () => {qtdEmails--; email3=''}"><i class="fas fa-minus"></i></button>
          </div>
          <div class="invalid-feedback">E-mail repetido!</div>
        </div>
      </div>
      <!-- Botão Add-Email -->
      <button
        v-if="qtdEmails < 3"
        class="btn btn-block btn-info"
        type="button"
        @click="qtdEmails++">
        <i class="fas fa-plus-square pr-2"></i>Adicionar outro e-mail
      </button>
      <hr>

      <!-- Telefones -->
      <h5 class="text-center">Telefones</h5>
      <p v-if="telefones.length===0" class="text-center mb-2">não há telefone registrado</p>
      <template v-for="(tel, i) in telefones">
        <div class="form-group" :key="i">
          <div class="input-group">
            <!-- Icone [prepend] -->
            <div class="input-group-prepend">
              <div class="input-group-text">
                <i v-if="tel.tipo === 1" class="fas fa-home"></i>
                <i v-else-if="tel.tipo === 2" class="fas fa-briefcase"></i>
                <i v-else-if="tel.tipo === 3" class="fas fa-mobile-alt px-1"></i>
              </div>
            </div>
            <!-- Input -->
            <the-mask type="tel" class="form-control" :mask="['(##) ####-####', '(##) #####-####']" v-model="telefones[i].numero" required></the-mask>
            <!-- Btn Remover [append] -->
            <div class="input-group-append">
              <button class="btn btn-danger" type="button" @click="telefones.splice(i,1)">
                <i class="fas fa-minus"></i>
              </button>
            </div>
          </div>
          <small class="text-muted" v-if="tel.tipo === 1">fixo residencial</small>
          <small class="text-muted" v-else-if="tel.tipo === 2">fixo comercial</small>
          <small class="text-muted" v-else-if="tel.tipo === 3">celular</small>
        </div>
      </template>
      <div class="dropdown">
        <button class="btn btn-block btn-info dropdown-toggle" type="button" data-toggle="dropdown"><i class="fas fa-plus-square pr-2"></i>Adicionar um telefone</button>
        <div class="dropdown-menu">
          <button class="dropdown-item" type="button" @click="addTelefone(2)"><i class="fas fa-briefcase pr-2"></i>Fixo Comercial</button>
          <button class="dropdown-item" type="button" @click="addTelefone(3)"><i class="fas fa-mobile-alt pr-2 pl-1"></i>Celular</button>
        </div>
      </div>
    </div>
    <div class="card-footer text-center">
      <button type="button" class="btn btn-primary" @click="$router.push({ name: 'recadpj-2', params: {empresa: empresa} })">Voltar</button>
      <button type="submit" class="btn btn-success">Avançar</button>
    </div>
  </form>
</template>

<script>
  export default {
    name: "PjContato",
    props: {
      empresa: { required: true, type: Object },
      value: { default: () => ({}), type: Object }
    },
    data: () => ({
      email1: '',
      email2: '',
      email3: '',
      qtdEmails: 1,
      telefones: []
    }),
    methods: {
      submit() {
        if (!this.emailValido() || !this.telefoneValido()) return;

        const dados = this.value;
        const emails = [this.email1];
        if (this.email2) emails.push(this.email2);
        if (this.email3) emails.push(this.email3);
        dados.emails = emails;
        dados.telefones = this.telefones;
        this.$emit('input', dados);
        this.$router.push({ name: 'recadpj-4', params: {empresa: this.empresa} });
      },
      //Rotina interna
      popularCampos(e) {
        //Obtem os dados de forma inicial
        //Emails
        const emails = e.emails.filter(e => !!e);
        if (emails.length > 0) this.email1 = emails[0];
        if (emails.length > 1) this.email2 = emails[1];
        if (emails.length > 2) this.email3 = emails[2];
        this.qtdEmails = emails.length;
        //Telefones
        this.telefones = e.telefones.comercial.map(tel => ({tipo: 2, numero: tel}))
          .concat(
            e.telefones.celular.map(tel => ({tipo: 3, numero: tel}))
          );

        //Obtem os dados de forma cacheada (caso eles ja fossem preenchidos anteriormente)
        const dados = this.value;
        if (dados.emails) {
          if (dados.emails.length >= 1) this.email1 = dados.emails[0];
          if (dados.emails.length >= 2) this.email2 = dados.emails[1];
          if (dados.emails.length >= 3) this.email3 = dados.emails[2];
        }
        if (dados.telefones) this.telefones = dados.telefones;
      },
      addTelefone(tipo) {
        const qtdAtual = this.telefones.filter(tel => tel.tipo === tipo).length;
        if (qtdAtual >= 3) {
          this.$toastr.info("Você já possui 3 telefones deste tipo");
          return;
        }
        this.telefones.push({tipo: tipo, numero: ''});
      },
      emailValido() {
        //Higieniza erros trataveis nos campos de email e retorna um boleano que informa se o formulário está aceitavel.
        //Realiza um trim nos emails
        this.email1 = this.email1.trim();
        this.email2 = this.email2.trim();
        this.email3 = this.email3.trim();

        //Re-organiza se deixou um campo antecessor vazio
        if (!this.email2 && this.email3) {
          this.email2 = this.email3;
          this.email3 = '';
          this.qtdEmails--;
        }
        if (!this.email1 && this.email2) {
          this.email1 = this.email2;
          this.email2 = '';
          this.qtdEmails--;
        }

        //Há pelo menos 1 email?
        if (!this.email1 && !this.email2 && !this.email3) {
          this.$toastr.warning("Insira pelo menos um e-mail");
          return false;
        }

        //Há email repetido?
        if ((this.email1 && this.email2 && this.email1===this.email2) || (this.email1 && this.email3 && this.email1===this.email3) || (this.email2 && this.email3 && this.email2===this.email3)) {
          this.$toastr.warning("Há e-mails repetidos");
          return false;
        }
        return true;
      },
      telefoneValido() {
        //verifica se há número repetido
        if (!this.telefones.length) {
          this.$toastr.warning('Adicione pelo menos um telefone');
          return false;
        }
        const numeros = this.telefones.map(tel => tel.numero);
        const repeticoes = numeros.filter(num => numeros.filter(numero => num === numero).length > 1);
        if (repeticoes.length > 0) {
          this.$toastr.warning('Há telefones repetidos');
          return false;
        }
        // if (!this.telefones.filter(tel => tel.tipo === 3).length) {
        //   this.$toastr.warning('Você precisa registrar um celular');
        //   return false;
        // }
        return true;
      }
    },
    mounted() {
      this.popularCampos(this.empresa);
    },
    beforeRouteLeave(to, from, next) {
      to.params.empresa = this.empresa;
      next();
    }
  }
</script>

